.App {
  height: 100vh;
  width: 100vw;
  display: flex;
  flex-direction: column;
}

.header {
  background-color: #dfdfdf;
  gap:8px;
  height: 40px;
  padding: 0 8px;
  display: flex;
  align-items: center;
}

.footer {
  height: 40px;
  background-color: #efefef;
}

.container {
  height: calc(100vh - 40px);
  display: flex;
}

.content {
  flex: 1;
  display: flex;
  flex-direction: column;
}

.content-head {
  display: flex;
  gap:4px;
  padding: 8px;
  background-color: #efefef;
}

.content-foot {
  padding: 8px;
  background-color: #efefef;
  text-align: center;
}

.content-main {
  flex: 1;
  overflow: auto;
}

button {
  border-radius: 0 !important;
}